.gpt3__cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem;
  margin: 4rem;
  border-radius: 1rem;
  background: var(--gradient-bar);
}

.gpt3__cta p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 0.75rem;
  line-height: 1.875rem;
  color: #0e0e0e;
}

.gpt3__cta h3 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 1.5rem;
  line-height: 2.8125rem;
  color: #0e0e0e;
}

.gpt3__cta-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 2rem;
}

.gpt3__cta-btn button {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 1.125rem;
  line-height: 1.875rem;
  background: #000000;
  color: #fff;
  border: none;
  border-radius: 40px;
  padding: 1rem 3rem;
  outline: none;
  cursor: pointer;
  white-space: nowrap;
  min-width: 150px;
}

@media screen and (max-width: 700px) {
  .gpt3__cta {
    flex-direction: column;
  }
  .gpt3__cta-btn {
    margin: 2rem 0 0;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__cta {
    margin: 4rem 2rem;
  }
  .gpt3__cta-content h3 {
    font-size: 1.125rem;
    line-height: 1.875rem;
  }
  .gpt3__cta-btn button {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
}
