.gpt3__blog-container_article {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--color-footer);
}

.gpt3__blog-container_article-img {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: var(--color-footer);
}

.gpt3__blog-container_article-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gpt3__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  height: 100%;
}

.gpt3__blog-container_article-content p {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 0.625rem;
  line-height: 2.1875rem;
  color: #fff;
}

.gpt3__blog-container_article-content h3 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 1.5625rem;
  line-height: 1.875rem;
  color: #fff;
  margin-bottom: 5rem;
  cursor: pointer;
}

.gpt3__blog-container_article-content p:last-child {
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .gpt3__blog-container_article-content h3 {
    font-size: 1.125rem;
    line-height: 1.5625rem;
  }
  .gpt3__blog-container_article-img img {
    object-fit: contain;
  }
}
