.gpt3__features-container__feature {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;

  margin-bottom: 1rem;
}

.gpt3__features-container__feature-title {
  flex: 1;
  max-width: 180px;
  margin-right: 2rem;
  margin-bottom: 1.5rem;
}

.gpt3__features-container__feature-title h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 1.125rem;
  line-height: 1.5rem;
  letter-spacing: -0.04em;
  color: #fff;
}

.gpt3__features-container__feature-title div {
  width: 38px;
  height: 3px;

  background: var(--gradient-bar);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 0.5rem;
}

.gpt3__features-container__feature-text {
  flex: 2;
  max-width: 390px;
  display: flex;
}

.gpt3__features-container__feature-text p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 0.875rem;
  line-height: 1.875rem;
  color: var(--color-text);
  padding-right: 0.75rem;
}

@media sceen and (max-width: 550px) {
  .gpt3__features-container__feature-title {
    font-size: 0.875rem;
    line-height: 1.375rem;
  }
  .gpt3__features-container__feature-text p {
    font-size: 0.75rem;
    line-height: 1.25rem;
  }
  .gpt3__features-container__feature {
    margin: 1rem 0;
  }
}
