.gpt3__possibility {
  display: flex;
  flex-direction: row;
}

.gpt3__possibility-img {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 2rem;
}

.gpt3__possibility-img img {
  width: 100%;
  height: 100%;
}

.gpt3__possibility-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}

.gpt3__possibility-content h4 {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.875rem;
  color: #71e5ff;
}

.gpt3__possibility-content h4:last-child {
  color: var(--color-subtext);
  margin-bottom: 2rem;
}

.gpt3__possibility-content h1 {
  width: 75%;
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 2.125rem;
  line-height: 2.8125rem;
  margin: 1.5rem 0;
}

.gpt3__possibility-content p {
  width: 80%;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.875rem;
  color: var(--color-text);
  margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
  .gpt3__possibility {
    flex-direction: column;
  }
  .gpt3__possibility-img {
    margin: 1rem 0;
  }
  .gpt3__possibility-content {
    margin-top: 2rem;
  }
  .gpt3__possibility-content h1 {
    width: 100%;
  }
  .gpt3__possibility-content p {
    width: 100%;
  }
}
